* {
  color: #fff;
}

.cart {
  margin: auto;
  max-width: 1260px;
  display: grid;
  grid-template-columns: 65% 35%;

  &__products {
    margin-right: 30px;
  }

  &__checkout {
    margin-left: 30px;
  }

  &__image {
    width: 240px;
  }
}

.checkout__methods {
  margin-top: 30px;
}

@media (max-width: 992px) {
  .cart {
    grid-template-columns: repeat(1, 1fr);

    &__products {
      margin-right: 20px;
      margin-left: 20px;
    }

    &__checkout {
      margin-top: 20px;
      margin-right: 20px;
      margin-left: 20px;
    }
  }
}
